import React, { Component } from "react";
import { Link } from "react-router-dom";
import { getSeriesByName } from "../services/dataService";

export default class Search extends Component{
    constructor(){
        super();

        this.state={
            seriesData: [],
            searchString: "",
        }
    }

    searchedSerie = event => {
        const searchString = event.target.value;

        this.setState({
            searchString
        })

        getSeriesByName(
            searchString,
            ({ data }) => { this.setState({ seriesData: data }); },
            error => {
                console.warn(error);
            }
        );
    }

    emptyInputField = () => {
        this.setState({
            searchString: ""
        })
    }

    render(){
        return(
            <div>
                <input 
                className="form-control mr-sm-2 " 
                onChange={this.searchedSerie} 
                value={this.state.searchString} 
                type="search" 
                placeholder="Search" 
                aria-label="Search"
                />
                { this.state.searchString && 
                <ul className="list-group">
                    {this.state.seriesData.map((series,i) =>
                            <li className="list-group-item " key={i} onClick={this.emptyInputField}>
                                <Link to={`/single/${series.show.id}`}>
                                    {series.show.name}
                                </Link>
                            </li>
                        )
                    }
                </ul>}
            </div>
        )
    }
}


